import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {AfricleanUserModel} from '../../../../models/africlean-user.model';
import {AfricleanPayementModel} from '../../../../models/finances/africlean-payement.model';
import {AfricleanSessionProvider} from '../../../../providers/africlean-session/africlean-session.provider';
import {AfricleanHttpPayementProvider} from '../../../../providers/africlean-http/africlean-http-payement.provider';
import {AfricleanDateProvider} from '../../../../providers/africlean-date/africlean-date.provider';
import {AfricleanObservablesProvider} from '../../../../providers/africlean-observables/africlean-observables.provider';
import {AfricleanConfigProvider} from "../../../../providers/africlean-config/africlean-config.provider";

@Component({
    selector: 'app-finance-management',
    templateUrl: './finance-management.page.html',
    styleUrls: ['./finance-management.page.scss'],
})
export class FinanceManagementPage implements OnInit {
    public payeSubscriptions: string;

    public payeEmploye: string;

    public payeSubscriptionCheck = false;

    public payeEmployeCheck = false;

    /**
     * Liste des payements
     */
    @Input() public payements: { user: AfricleanUserModel, payement: AfricleanPayementModel } [];
    @Input() public payementsTMP: { user: AfricleanUserModel, payement: AfricleanPayementModel } [];

    @Input() public payementsEmploye: { user: AfricleanUserModel, payement: AfricleanPayementModel } [];
    @Input() public payementsTMPEmploye: { user: AfricleanUserModel, payement: AfricleanPayementModel } [];


    /**
     * numéro des pages
     */
    public positions = [];
    /**
     * numéro des pages
     */
    public positionsEmploye = [];

    /**
     * Position de la page courante
     */
    @Input() public currentPage = 1;


    /**
     * Le nombre de clients par pages
     */
    @Input() public nbrPaiementPerPageEmploye = 5;

    /**
     * Position de la page courante
     */
    @Input() public currentPageEmploye = 1;


    /**
     * Le nombre de clients par pages
     */
    @Input() public nbrPaiementPerPage = 5;

    /**
     * client concerné
     */
    @Input() public userTMP: AfricleanUserModel;

    /**
     * D'ou est appelé le component
     */
    @Input() public source: string;

    /**
     * role de l'utilisateur
     */
    @Input() public role: string;

    /**
     * id de l'utilisateur
     */
    @Input() public userId: string;

    /**
     * La limite pour récuperer les données
     */
    public limit = 10;

    /**
     * Données à ignorer
     */
    public skip = 0;

    /**
     * La limite pour récuperer les données
     */
    public limitEmploye = 10;

    /**
     * Données à ignorer
     */
    public skipEmploye = 0;

    public isWeb: boolean;


    constructor(private session: AfricleanSessionProvider,
                private payementProvider: AfricleanHttpPayementProvider,
                private dateProvider: AfricleanDateProvider,
                private observableProvider: AfricleanObservablesProvider,
                private config: AfricleanConfigProvider) {
    }

    ngOnInit() {
        this.getUser();
        this.isWeb = this.config.isWeb();
        this.observableProvider.loadPayement.subscribe(
            (result) => {
                this.payements = [];
                this.payementsTMP = [];
                this.positions = [];
                this.getPayements('abonnement');
            }
        );
        this.observableProvider.loadPayementEmploye.subscribe(
            (result) => {
                this.payementsEmploye = [];
                this.payementsTMPEmploye = [];
                this.positionsEmploye = [];
                this.getPayements('employe');
            }
        );

    }

    /**
     * Utilisateur temporaire
     */
    public async getUser() {
        this.role = await this.session.getItem('role');
        this.userId = await this.session.getItem('userId');
        this.userTMP = await this.session.getItem('userTMP');
        (this.role === 'COMMERCIAL') ?  this.getPayements('abonnement') : ' ';
    }

    /**
     * Switch vers le payement de l'abonnement
     * @param event evenement
     */
    public togglePaySubscription(event: any) {
        this.payeSubscriptionCheck = event.detail.checked;
        this.payeEmployeCheck = !this.payeSubscriptionCheck;
        if (this.payements && this.payements.length > 0) {
            this.payements = [];
            this.payementsTMP = [];
            this.positions = [];

        } else {
            this.getPayements('abonnement');
        }
    }

    /**
     * Switch vers le payement des employés
     * @param event événement
     */
    public togglePayeEmploye(event: any) {
        this.payeEmployeCheck = event.detail.checked;
        this.payeSubscriptionCheck = !this.payeEmployeCheck;
        if (this.payementsEmploye && this.payementsTMPEmploye.length > 0) {
            this.payementsEmploye = [];
            this.payementsTMPEmploye = [];
            this.positionsEmploye = [];
        } else {
            this.getPayements('employe');
        }
    }

    sendPosition(position) {
        this.currentPage = position;
        if (position % 2 === 0 && !this.positions.includes(position)) {
            this.positions.push(position);
            this.skip = this.skip + this.limit;
            this.getPayements('abonnement');
        }
    }

    /**
     * Liste de payement
     */
    public async getPayements(typePayement) {
        let body: any = {};
        let url: string;
        this.role = await this.session.getItem('role');
        this.userId = await this.session.getItem('userId');

        if (typePayement === 'abonnement') {
            if (this.role === 'CLIENT') {
                url = 'payement/paiements/client/' + this.userId;
            } else {
                url = 'payement/paiements/';
            }
            body = {
                limit: this.limit,
                skip: this.skip
            };
        } else {
            if (this.role === 'COMMERCIAL' || this.role === 'INTERVENANT') {
                url = 'payement-employe/paiements/employe/' + this.userId;
            } else {
                url = 'payement-employe/paiements/';
            }
            body = {
                limit: this.limitEmploye,
                skip: this.skipEmploye
            };

        }
        this.payementProvider.postPaiment(url, body).subscribe(
            (payementList) => {

                if (!this.payements && !this.payementsTMP) {
                    this.payements = [];
                    this.payementsTMP = [];
                    this.positions = [];
                }
                if (!this.payementsEmploye && !this.payementsTMPEmploye) {
                    this.payementsEmploye = [];
                    this.payementsTMPEmploye = [];
                    this.positionsEmploye = [];
                }
                //  if (this.source === 'consult-subscribe-management') {
                if (typePayement === 'abonnement') {
                    this.payements = this.payements.concat(payementList.slice());
                    this.payementsTMP = this.payementsTMP.concat(payementList.slice());
                } else {
                    this.payementsEmploye = this.payementsEmploye.concat(payementList.slice());
                    this.payementsTMPEmploye = this.payementsTMPEmploye.concat(payementList.slice());
                }
                /*  } else {
                      this.payements = this.payements.concat(payementList.slice());
                      this.payementsTMP = this.payementsTMP.concat(payementList.slice());
                  }*/
            }
        );
    }

    sendPositionEmploye(position) {
        this.currentPageEmploye = position;
        if (position % 2 === 0 && !this.positionsEmploye.includes(position)) {
            this.positionsEmploye.push(position);
            this.skipEmploye = this.skipEmploye + this.limitEmploye;
            this.getPayements('employe');
        }
    }


    private _filter(name: any, list: any[]): any[] {
        const filterValue = name.toLowerCase();
        // tslint:disable-next-line:max-line-length
        return list.filter(item => (item.payement.payementCreatedAuthorId.authorId.toLowerCase().indexOf(filterValue) === 0));
    }
}
